

































































































































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { deepmerge } from '@zeta/utils';
import { extractValues, conditions, getTagLabel, applyFilters } from './utils';

@Component({
    name: 'DataFilter2',
})
export default class DataFilter2 extends Vue {
    @Prop() private value!: any;
    @Prop() private config!: any;
    @Prop() private defaultTags!: any;

    private filteredTags = [];
    private localConfig = [];
    private localData = [];
    private tags: any = [];
    private emittedData: any = [];
    private tagInputDisabled = false;

    private maxHeight = 365;

    private getFilteredTags(text: string) {
        this.filteredTags = this.localConfig.filter((option: any) => {
            return (
                option.label
                    .toString()
                    .toLowerCase()
                    .indexOf(text.toLowerCase()) >= 0
            );
        });
    }

    initData() {
        let config: any = deepmerge([], this.config);
        config = config.map((item: any) => {
            if (!item.conditions || item.conditions.length === 0) {
                item.conditions = conditions[item.type] || [];
            }
            item.values = extractValues(this.value, item.key);
            return {
                ...item,
                filteredValues: [],
                conditionValues: { key: null, value: [] },
                filterValues: [],
            };
        });
        this.localData = deepmerge([], this.value);
        this.localConfig = deepmerge([], config);
        config = this.defaultTags.length
            ? this.defaultTags.map((item: any) => {
                  const values: any = extractValues(this.value, item.key);
                  let obj = {
                      ...item,
                      conditionValues: { key: null, value: [] },
                      filteredValues:
                          values && values.length
                              ? item.filteredValues.filter(
                                    (realValue: any) => values.indexOf(realValue) > -1,
                                )
                              : item.filteredValues,
                      filterValues: values && values.length ? values : [],
                      values: values && values.length ? values : [],
                      hideClose: item.disabled ? true : false,
                  };
                  obj.tagLabel = getTagLabel(obj);
                  return obj;
              })
            : [];
        this.tags = config;
        this.filteredTags = this.localConfig.filter((item: any) => {
            let temp = this.tags.filter((data: any) => data.key === item.key);
            return !temp.length;
        });
        this.emitData();
    }

    private toggleDropDown(index: number) {
        const element: any = this.$refs['dropdown' + index];
        if (element) element[0].toggle();
    }
    private addTag(tag: any) {
        tag.tagLabel = tag.label;
        tag.filterValues = deepmerge([], tag.values);
        this.tags.push({ ...tag });
    }
    private removeTag(tag: any) {
        this.tags = this.tags.filter((item: any) => item.key !== tag.key);
    }
    private updateFilterData(tag: any, isAdded: boolean) {
        if (isAdded) {
            this.addTag(tag);
            this.filteredTags = this.filteredTags.filter((item: any) => item.key !== tag.key);
            this.$nextTick(function() {
                this.toggleDropDown(this.tags.length - 1);
            });
        } else {
            this.removeTag(tag);
            this.filteredTags = this.localConfig.filter((item: any) => {
                let temp = this.tags.filter((data: any) => data.key === item.key);
                return !temp.length;
            });
            this.emitData();
        }
    }
    private emitData() {
        this.emittedData = deepmerge([], this.tags);
        let data = applyFilters(this.localData, this.tags);
        this.$emit('filtered-values', this.emittedData);
        this.$emit('input', data);
    }
    private activeChange(value: boolean, ind: number) {
        for (let index = 0; index < this.tags.length; index++) {
            this.tags[index].disabled = index !== ind ? value : false;
        }
        this.tagInputDisabled = value;
    }
    private getDateValues(dates: Date[]) {
        return dates.length ? [new Date(dates[0]), new Date(dates[1])] : [new Date(), new Date()];
    }
    private dateEntered(value: Date[], index: number) {
        this.tags[index].filteredValues = [
            new Date(value[0]).setHours(0, 0, 0),
            new Date(value[1]).setHours(23, 59, 59),
        ];
    }
    private saveFilterState(index: number) {
        this.tags[index].activeTab = this.tags[index].filteredValues.length ? 0 : 1;
    }
    private resetData(index: number) {
        if (this.emittedData[index]) {
            this.tags[index].filteredValues = deepmerge([], this.emittedData[index].filteredValues);
            this.tags[index].conditionValues = deepmerge(
                [],
                this.emittedData[index].conditionValues,
            );
        }
    }
    private closeDropDown(index: number, emitData: boolean) {
        this.toggleDropDown(index);
        if (emitData) {
            this.saveFilterState(index);
            this.emitData();
        } else {
            this.tags[index].activeTab = this.emittedData[index]
                ? this.emittedData[index].activeTab
                : 0;

            this.resetData(index);
        }
    }
    @Watch('emittedData')
    onTagChange(val: string) {
        for (let index = 0; index < val.length; index++) {
            this.tags[index].tagLabel = getTagLabel(val[index]);
        }
    }
    private created() {
        this.initData();
    }
    private onTabChange(value: string | number, index: number) {
        if (value) {
            this.resetFilteredValues(index);
        } else {
            this.resetConditionValues(index);
        }
    }
    private resetFilteredValues(index: number) {
        this.tags[index].filteredValues = [];
    }
    private resetConditionValues(index: number) {
        this.tags[index].conditionValues = { key: null, value: [] };
    }
    private filterValues(text: string, index: number) {
        this.tags[index].filterValues = [
            ...this.tags[index].values.filter(
                (item: string) => item.toLowerCase().indexOf(text.toLowerCase()) > -1,
            ),
        ];
    }
    private selectAll(checked: boolean, index: number) {
        if (checked) {
            this.tags[index].filteredValues = deepmerge([], this.tags[index].values);
        } else {
            this.resetFilteredValues(index);
        }
    }
    private updateValue(checked: boolean, value: string, index: number) {
        if (checked) {
            this.tags[index].filteredValues.push(value);
        } else {
            this.tags[index].filteredValues.splice(
                this.tags[index].filteredValues.indexOf(value),
                1,
            );
        }
    }
}
