import { deepmerge } from '@zeta/utils';
import dotProp from 'dot-prop';
export const conditions: any = {
    string: [
        {
            key: 'ie',
            value: 'Is empty',
            checker: (value: string) => {
                return value === '';
            },
        },
        {
            key: 'ine',
            value: 'Is not empty',
            checker: (value: string) => {
                return value !== '';
            },
        },
        {
            key: 'contains',
            value: 'Text contains',
            checker: (value: string, conditionValue: string[]) => {
                return value.toLowerCase().indexOf(conditionValue[0].toLowerCase()) > -1;
            },
        },
        {
            key: 'ncontains',
            value: 'Text does not contain',
            checker: (value: string, conditionValue: string[]) => {
                return value.toLowerCase().indexOf(conditionValue[0].toLowerCase()) === -1;
            },
        },
        {
            key: 'sw',
            value: 'Text starts with',
            checker: (value: string, conditionValue: string[]) => {
                return value.toLowerCase().startsWith(conditionValue[0].toLowerCase());
            },
        },
        {
            key: 'ew',
            value: 'Text ends with',
            checker: (value: string, conditionValue: string[]) => {
                return value.toLowerCase().endsWith(conditionValue[0].toLowerCase());
            },
        },
        {
            key: 'exact',
            value: 'Text is exactly',
            checker: (value: string, conditionValue: string[]) => {
                return value === conditionValue[0];
            },
        },
    ],
    number: [
        {
            key: 'lt',
            value: 'Less than',
            checker: (value: string, conditionValue: string[]) => {
                return parseFloat(value) < parseFloat(conditionValue[0]);
            },
        },
        {
            key: 'gt',
            value: 'Greater than',
            checker: (value: string, conditionValue: string[]) => {
                return parseFloat(value) > parseFloat(conditionValue[0]);
            },
        },
        {
            key: 'lte',
            value: 'Less than equal to',
            checker: (value: string, conditionValue: string[]) => {
                return parseFloat(value) <= parseFloat(conditionValue[0]);
            },
        },
        {
            key: 'gte',
            value: 'Greater than equal to',
            checker: (value: string, conditionValue: string[]) => {
                return parseFloat(value) >= parseFloat(conditionValue[0]);
            },
        },
        {
            key: 'et',
            value: 'Equal to',
            checker: (value: string, conditionValue: string[]) => {
                return parseFloat(value) === parseFloat(conditionValue[0]);
            },
        },
        {
            key: 'net',
            value: 'Not equal to',
            checker: (value: string, conditionValue: string[]) => {
                return parseFloat(value) !== parseFloat(conditionValue[0]);
            },
        },
        {
            key: 'btw',
            value: 'Range',
            checker: (value: string, conditionValue: string[]) => {
                return (
                    parseFloat(value) >= parseFloat(conditionValue[0]) &&
                    parseFloat(value) <= parseFloat(conditionValue[1])
                );
            },
        },
    ],
};
export const getCondition = function(tag: any, key: string) {
    return tag.conditions
        ? tag.conditions.filter((item: any) => item.key === key)
        : conditions[tag.type].filter((item: any) => item.key === key);
};
export const getTagLabel = function(emittedData: any) {
    let label: string = '';
    if (emittedData && emittedData.type === 'date') {
        label = `${(emittedData.valueFormatter &&
            emittedData.valueFormatter(emittedData.filteredValues[0])) ||
            emittedData.filteredValues[0]} - ${(emittedData.valueFormatter &&
            emittedData.valueFormatter(emittedData.filteredValues[1])) ||
            emittedData.filteredValues[1]}`;
    } else if (emittedData && emittedData.filteredValues.length) {
        label =
            emittedData.filteredValues.length === 1
                ? `${(emittedData.valueFormatter &&
                      emittedData.valueFormatter(emittedData.filteredValues[0])) ||
                      emittedData.filteredValues[0]}`
                : emittedData.filteredValues.length === emittedData.values.length
                  ? `All Selected`
                  : `${emittedData.filteredValues.length} selected`;
    } else if (
        emittedData &&
        emittedData.conditionValues.value &&
        emittedData.conditionValues.value.length
    ) {
        const condition = getCondition(emittedData, emittedData.conditionValues.key)[0];
        label = `${condition ? condition.value.toLowerCase() : ''} ${emittedData.conditionValues
            .value}`;
    }
    return `${emittedData.label + (label ? ': ' + label : '')}`;
};

export const extractValues = (data: any, key: any) => {
    let result: any = [];
    for (let index = 0; index < data.length; index++) {
        const element = data[index];
        const value = dotProp.get(element, key);
        if (result.indexOf(value) === -1) result.push(value);
    }
    return result;
};

export const applyFilters = (data: any, filters: any) => {
    let tempData: any = deepmerge([], data);
    filters.map((item: any) => {
        tempData = tempData.filter((dataItem: any) => {
            const value: any = dotProp.get(dataItem, item.key);
            if (item.filteredValues && item.filteredValues.length) {
                if (item.type === 'date') {
                    return value >= item.filteredValues[0] && value <= item.filteredValues[1];
                }
                return item.filteredValues.indexOf(value) > -1;
            } else if (item.conditionValues && item.conditionValues.key && item.conditions.length) {
                const config =
                    item.conditions.filter(
                        (conf: any) => conf.key === item.conditionValues.key,
                    )[0] || null;
                return config && config.checker(dataItem[item.key], item.conditionValues.value);
            }
            return true;
        });
    });
    return tempData;
};

/* Polyfills */
if (!String.prototype.startsWith) {
    Object.defineProperty(String.prototype, 'startsWith', {
        value: function(search: string, rawPos: number) {
            var pos = rawPos > 0 ? rawPos | 0 : 0;
            return this.substring(pos, pos + search.length) === search;
        },
    });
}
if (!String.prototype.endsWith) {
    String.prototype.endsWith = function(search: string, this_len: number) {
        if (this_len === undefined || this_len > this.length) {
            this_len = this.length;
        }
        return this.substring(this_len - search.length, this_len) === search;
    };
}
